<template>
  <div class="straightBasket">
    <v-container
      fluid
      grid-list-xl>
      <!-- 語系選單(需隱藏) -->
      <SelectSelectLanguage v-show="false"/>
      <v-layout
        row
        wrap
        class="p-0 my-0">
        <v-flex
          md6
          xs12
          tag="h3"
          class="my-0 pb-3">
          {{ $ln(gname) }}-{{ $ln('單式入球數') }}
        </v-flex>

        <SportBallStatus
          :dataSelect.sync="selectChangeItems.selected"
          :items="selectChangeItems.options"
          title="拆帳"
        />
        <!-- 每秒更新 -->
        <CommonCounter
          :reciprocalItems.sync="reciprocalItems"
          :updateTableList="updateTableList"
        />
      </v-layout>

      <div>
        {{ $ln('帳務時間') }}：{{ count_date }} {{ $ln('球隊') }}：{{ main_team }}(H)VS {{ visit_team }}
      </div>
      {{ league_name }}-{{ transType }}
      <div class="pt-1">
        <div class="rwdTable no-rwd-table">
          <div class="v-table__overflow">
            <table class="v-datatable v-table theme--light goals">
              <thead>
                <tr>
                    <th v-for="(header, i) in goalsListAll.headers" :key="i" :colspan="header.colspan">{{ header.text }}</th>
                  </tr>
                  <tr v-if="goalsListAll.List.length > 0">
                    <th v-for="(header, i) in goalsListAll.headers2" :key="i">{{ header.handicap }}</th>
                  </tr>
              </thead>
              <tbody v-if="goalsListAll.List.length > 0">
                <tr>
                  <td 
                    :class="item.switch === 'N' ? 'grey lighten-3' : ''"
                    v-for="(item, i) in goalsListAll.List" :key="i">
                      {{ item.compensate }}&nbsp; 
                      <router-link :to="{ path:'/manage/sport/soccer/CheckView' , query: { type:16, Posting: $route.query.Posting, ball:10,
                          act:'normal',game:'A', gameSN: $route.query.gameSN, game_type: $route.query.game_type, handicap: item.handicap } }"
                          target="_blank">{{ item.count ? item.count : 0 }}
                      </router-link>/{{ item.gold ? item.gold : 0 }}
                  </td>
                </tr>
              </tbody>
              <tbody v-else>
                <tr><td :colspan="goalsListAll.headers[0].colspan" align="center">{{ $ln(goalsListAll.showNoData) }}</td></tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </v-container>
  </div>
</template>

<script>
import Goals from '@/mixins/sport/Goals.vue'

export default {
  name: 'SoccerGoals',
  mixins:[Goals],
  data () {
    return {
      gname: '足球',
      goalsListAll: {
        show: true,
        showNoData: '目前無資料',
        headers: [
          { text: '全場',colspan: 0, show: true, align: 'center', sortable: false },
        ],
        headers2: [],
        List: []
      },
      // 拆帳選擇
      selectChangeItems: {
        selected: 1,
        options: [
          { text: '100％', value: 1 },
          { text: '自己', value: 0 }
        ]
      },
      
      // 秒數倒數更新
      reciprocalItems: {
        selected: '',
        options: [
          { text: '每180秒更新', value: 180 },
          { text: '每60秒更新', value: 60 },
          { text: '每5秒更新', value: 5 },
          { text: '不更新', value: '' }
        ],
        // 倒數 timer
        updateListTimer: null,
        // 當前秒數
        currentSpeed: '',
        // 確定更新秒數
        confirmSpeed: ''
      },
      dataList: [],
      game_time: '',
      occupy: '',
      count_date: '',
      main_team: '',
      visit_team: '',
      league_name: '',
      transType: '',
      posting: '',
      game_serial_number: '',
      game_type: ''
    }
  }
}
</script>
<style scoped>
.goals {
    max-width: 40%;
}
.width25 {
    width: 25%;
}
</style>